import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import adminService from '../api/adminService';
import DarkModeToggle from './DarkModeToggle';
import Logo from './Logo';

const AdminDashboard = () => {
  const navigate = useNavigate();
  const { logout, user } = useAuth();
  const [hasAnimated, setHasAnimated] = useState(false);
  const [activeTab, setActiveTab] = useState('overview');
  const [metrics, setMetrics] = useState(null);
  const [stats, setStats] = useState(null);
  const [loading, setLoading] = useState(false);
  const [period, setPeriod] = useState('30d');

  useEffect(() => {
    const timer = setTimeout(() => setHasAnimated(true), 100);
    return () => clearTimeout(timer);
  }, []);

  useEffect(() => {
    loadDashboardData();
  }, []);

  const loadDashboardData = async () => {
    setLoading(true);
    try {
      const [metricsData, statsData] = await Promise.all([
        adminService.getQualityMetrics(period),
        adminService.getTransactionStats()
      ]);
      setMetrics(metricsData);
      setStats(statsData);
    } catch (error) {
      console.error('Failed to load dashboard data:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleLogout = () => {
    logout();
    navigate('/login');
  };

  const formatCurrency = (amount, currencyCode = 'NGN') => {
    const symbols = {
      'NGN': '',
      'USD': '$',
      'GBP': '',
      'EUR': '',
      'CAD': 'C$',
      'AUD': 'A$',
      'JPY': '',
      'CHF': 'CHF ',
      'ZAR': 'R'
    };
    return `${symbols[currencyCode] || ''}${amount?.toLocaleString() || '0'}`;
  };

  const formatPercentage = (value) => {
    return `${(value * 100).toFixed(1)}%`;
  };

  const formatTime = (hours) => {
    if (hours < 1) {
      return `${Math.round(hours * 60)} minutes`;
    }
    return `${hours.toFixed(1)} hours`;
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-white dark:bg-dark-bg transition-colors duration-300 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-blue mx-auto mb-4"></div>
          <p className="text-neutral-dark dark:text-dark-text">Loading dashboard...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-white dark:bg-dark-bg transition-colors duration-300">
      {/* Header */}
      <header className={`flex justify-between items-center px-4 lg:px-20 py-4 lg:py-6 ${hasAnimated ? 'animate-slide-in-down animate-once' : 'opacity-0'}`}>
        {/* Logo */}
        <div className="flex items-center">
          <Logo className="w-10 h-7 lg:w-13 lg:h-9 mr-3" />
          <div className="text-black/80 dark:text-dark-text font-times text-lg lg:text-2xl font-bold transition-colors duration-300">
            FLOWWAVE ADMIN
          </div>
        </div>

        {/* Header Actions */}
        <div className="flex items-center gap-4 lg:gap-10">
          <div className="flex items-center gap-2">
            <div className="w-8 h-8 bg-green-500 rounded-full flex items-center justify-center">
              <span className="text-white text-xs font-bold">A</span>
            </div>
            <span className="text-sm font-medium text-neutral-dark dark:text-dark-text">
              {user?.email || 'Admin'}
            </span>
          </div>

          <DarkModeToggle />

          <button
            onClick={handleLogout}
            className="px-4 py-2 lg:px-6 lg:py-3 border-2 border-primary-blue text-primary-blue rounded-lg font-bold text-sm lg:text-lg hover:bg-primary-blue hover:text-white transition-all duration-300"
          >
            Log Out
          </button>
        </div>
      </header>

      {/* Main Content */}
      <main className="flex flex-col px-4 lg:px-0 pb-24">
        <div className={`w-full max-w-7xl mx-auto ${hasAnimated ? 'animate-fade-in-up animate-once' : 'opacity-0'}`} style={{ animationDelay: '0.2s' }}>
          {/* Tab Navigation */}
          <div className="flex space-x-1 mb-8 border-b border-gray-200">
            {['overview', 'transactions', 'metrics', 'users'].map((tab) => (
              <button
                key={tab}
                onClick={() => setActiveTab(tab)}
                className={`px-6 py-3 font-medium text-sm capitalize transition-colors duration-200 border-b-2 ${
                  activeTab === tab
                    ? 'text-primary-blue border-primary-blue'
                    : 'text-neutral-gray border-transparent hover:text-neutral-dark'
                }`}
              >
                {tab}
              </button>
            ))}
          </div>

          {/* Overview Tab */}
          {activeTab === 'overview' && (
            <div className="space-y-6">
              {/* Key Metrics */}
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
                <div className="bg-white p-6 rounded-lg border border-gray-200 shadow-sm">
                  <div className="flex items-center justify-between mb-2">
                    <h3 className="text-sm font-medium text-neutral-gray">Total Transactions</h3>
                    <div className="w-8 h-8 bg-blue-100 rounded-full flex items-center justify-center">
                      <svg width="16" height="16" viewBox="0 0 24 24" fill="none" className="text-blue-600">
                        <path d="M13 7H18V12H13M6 12H11V7H6M13 12V17H18V12M11 12V7H6V12M11 12V17H6V12" stroke="currentColor" strokeWidth="2"/>
                      </svg>
                    </div>
                  </div>
                  <p className="text-2xl font-bold text-neutral-dark">
                    {stats?.totalTransactions?.toLocaleString() || '0'}
                  </p>
                </div>

                <div className="bg-white p-6 rounded-lg border border-gray-200 shadow-sm">
                  <div className="flex items-center justify-between mb-2">
                    <h3 className="text-sm font-medium text-neutral-gray">Total Volume</h3>
                    <div className="w-8 h-8 bg-green-100 rounded-full flex items-center justify-center">
                      <svg width="16" height="16" viewBox="0 0 24 24" fill="none" className="text-green-600">
                        <path d="M12 2L2 7V17L12 22L22 17V7L12 2Z" stroke="currentColor" strokeWidth="2"/>
                      </svg>
                    </div>
                  </div>
                  <p className="text-2xl font-bold text-neutral-dark">
                    {formatCurrency(stats?.totalVolume)}
                  </p>
                </div>

                <div className="bg-white p-6 rounded-lg border border-gray-200 shadow-sm">
                  <div className="flex items-center justify-between mb-2">
                    <h3 className="text-sm font-medium text-neutral-gray">Success Rate</h3>
                    <div className="w-8 h-8 bg-green-100 rounded-full flex items-center justify-center">
                      <svg width="16" height="16" viewBox="0 0 24 24" fill="none" className="text-green-600">
                        <path d="M9 12L11 14L15 10" stroke="currentColor" strokeWidth="2"/>
                      </svg>
                    </div>
                  </div>
                  <p className="text-2xl font-bold text-green-600">
                    {formatPercentage(metrics?.successRate || 0)}
                  </p>
                </div>

                <div className="bg-white p-6 rounded-lg border border-gray-200 shadow-sm">
                  <div className="flex items-center justify-between mb-2">
                    <h3 className="text-sm font-medium text-neutral-gray">Avg Processing Time</h3>
                    <div className="w-8 h-8 bg-orange-100 rounded-full flex items-center justify-center">
                      <svg width="16" height="16" viewBox="0 0 24 24" fill="none" className="text-orange-600">
                        <path d="M12 2V12M12 12L16 8M12 12L8 8M12 12V22" stroke="currentColor" strokeWidth="2"/>
                      </svg>
                    </div>
                  </div>
                  <p className="text-2xl font-bold text-neutral-dark">
                    {formatTime(metrics?.averageProcessingTime || 0)}
                  </p>
                </div>
              </div>

              {/* Transaction Status Breakdown */}
              <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                <div className="bg-white p-6 rounded-lg border border-gray-200 shadow-sm">
                  <h3 className="text-lg font-semibold text-neutral-dark mb-4">Transaction Status</h3>
                  <div className="space-y-3">
                    {stats?.transactionsByStatus?.map((status) => (
                      <div key={status.status} className="flex items-center justify-between">
                        <div className="flex items-center gap-3">
                          <div className={`w-3 h-3 rounded-full ${
                            status.status === 'COMPLETED' ? 'bg-green-500' :
                            status.status === 'PROCESSING' ? 'bg-blue-500' :
                            status.status === 'PENDING' ? 'bg-yellow-500' :
                            'bg-red-500'
                          }`}></div>
                          <span className="text-sm font-medium">{status.status}</span>
                        </div>
                        <div className="text-right">
                          <div className="text-sm font-bold">{status.count}</div>
                          <div className="text-xs text-neutral-gray">{formatPercentage(status.percentage)}</div>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>

                <div className="bg-white p-6 rounded-lg border border-gray-200 shadow-sm">
                  <h3 className="text-lg font-semibold text-neutral-dark mb-4">Top Currencies</h3>
                  <div className="space-y-3">
                    {stats?.topCurrencies?.slice(0, 5).map((currency) => (
                      <div key={currency.currency} className="flex items-center justify-between">
                        <div className="flex items-center gap-3">
                          <div className="w-8 h-8 bg-primary-blue/10 rounded-full flex items-center justify-center">
                            <span className="text-xs font-bold text-primary-blue">{currency.currency}</span>
                          </div>
                          <span className="text-sm font-medium">{currency.currency}</span>
                        </div>
                        <div className="text-right">
                          <div className="text-sm font-bold">{currency.count}</div>
                          <div className="text-xs text-neutral-gray">{formatCurrency(currency.volume)}</div>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          )}

          {/* Metrics Tab */}
          {activeTab === 'metrics' && (
            <div className="space-y-6">
              {/* Period Selector */}
              <div className="flex items-center justify-between mb-6">
                <h2 className="text-2xl font-bold text-neutral-dark">Quality Metrics</h2>
                <select
                  value={period}
                  onChange={(e) => {
                    setPeriod(e.target.value);
                    loadDashboardData();
                  }}
                  className="px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-blue"
                >
                  <option value="7d">Last 7 days</option>
                  <option value="30d">Last 30 days</option>
                  <option value="90d">Last 90 days</option>
                </select>
              </div>

              {/* Metrics Grid */}
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                <div className="bg-white p-6 rounded-lg border border-gray-200 shadow-sm">
                  <h3 className="text-sm font-medium text-neutral-gray mb-2">Success Rate</h3>
                  <p className="text-3xl font-bold text-green-600">
                    {formatPercentage(metrics?.successRate || 0)}
                  </p>
                  <p className="text-sm text-neutral-gray mt-1">
                    {metrics?.completedTransactions || 0} of {metrics?.totalTransactions || 0} successful
                  </p>
                </div>

                <div className="bg-white p-6 rounded-lg border border-gray-200 shadow-sm">
                  <h3 className="text-sm font-medium text-neutral-gray mb-2">Failure Rate</h3>
                  <p className="text-3xl font-bold text-red-600">
                    {formatPercentage(metrics?.failureRate || 0)}
                  </p>
                  <p className="text-sm text-neutral-gray mt-1">
                    {metrics?.failedTransactions || 0} failed transactions
                  </p>
                </div>

                <div className="bg-white p-6 rounded-lg border border-gray-200 shadow-sm">
                  <h3 className="text-sm font-medium text-neutral-gray mb-2">Average Processing Time</h3>
                  <p className="text-3xl font-bold text-neutral-dark">
                    {formatTime(metrics?.averageProcessingTime || 0)}
                  </p>
                  <p className="text-sm text-neutral-gray mt-1">
                    Fastest: {formatTime(metrics?.fastestProcessingTime || 0)}
                  </p>
                </div>

                <div className="bg-white p-6 rounded-lg border border-gray-200 shadow-sm">
                  <h3 className="text-sm font-medium text-neutral-gray mb-2">Slowest Processing</h3>
                  <p className="text-3xl font-bold text-orange-600">
                    {formatTime(metrics?.slowestProcessingTime || 0)}
                  </p>
                  <p className="text-sm text-neutral-gray mt-1">
                    Needs attention
                  </p>
                </div>

                <div className="bg-white p-6 rounded-lg border border-gray-200 shadow-sm md:col-span-2">
                  <h3 className="text-sm font-medium text-neutral-gray mb-4">Processing Time Distribution</h3>
                  <div className="space-y-2">
                    {metrics?.processingTimes?.slice(0, 10).map((time, index) => (
                      <div key={index} className="flex items-center justify-between py-2 border-b border-gray-100 last:border-0">
                        <span className="text-sm">Transaction {index + 1}</span>
                        <span className="text-sm font-medium">{formatTime(time)}</span>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          )}

          {/* Transactions Tab */}
          {activeTab === 'transactions' && (
            <div className="space-y-6">
              <div className="flex items-center justify-between mb-6">
                <h2 className="text-2xl font-bold text-neutral-dark">Transaction Management</h2>
                <button
                  onClick={() => navigate('/admin/transactions')}
                  className="px-4 py-2 bg-primary-blue text-white rounded-lg hover:bg-primary-blue/90 transition-colors"
                >
                  View All
                </button>
              </div>

              <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                {/* Pending Transactions */}
                <div className="bg-white p-6 rounded-lg border border-gray-200 shadow-sm">
                  <h3 className="text-lg font-semibold text-neutral-dark mb-4 text-yellow-600">
                    Pending Transactions ({stats?.pendingTransactions || 0})
                  </h3>
                  <div className="space-y-3">
                    {/* This would be populated with actual pending transactions */}
                    <p className="text-neutral-gray text-sm">Click "View All" to manage pending transactions</p>
                  </div>
                </div>

                {/* Processing Transactions */}
                <div className="bg-white p-6 rounded-lg border border-gray-200 shadow-sm">
                  <h3 className="text-lg font-semibold text-neutral-dark mb-4 text-blue-600">
                    Processing Transactions ({stats?.processingTransactions || 0})
                  </h3>
                  <div className="space-y-3">
                    {/* This would be populated with actual processing transactions */}
                    <p className="text-neutral-gray text-sm">Click "View All" to manage processing transactions</p>
                  </div>
                </div>
              </div>
            </div>
          )}

          {/* Users Tab */}
          {activeTab === 'users' && (
            <div className="space-y-6">
              <div className="flex items-center justify-between mb-6">
                <h2 className="text-2xl font-bold text-neutral-dark">User Management</h2>
                <button
                  onClick={() => navigate('/admin/users')}
                  className="px-4 py-2 bg-primary-blue text-white rounded-lg hover:bg-primary-blue/90 transition-colors"
                >
                  Manage Users
                </button>
              </div>

              <div className="bg-white p-6 rounded-lg border border-gray-200 shadow-sm">
                <p className="text-neutral-gray text-sm">Click "Manage Users" to view and manage all user accounts</p>
              </div>
            </div>
          )}
        </div>
      </main>
    </div>
  );
};

export default AdminDashboard;
