import { apiClient } from '../utils/api';

class TransactionService {
  // Check Exchange Rates
  async checkExchangeRate(amount, fromCurrency, toCurrency) {
    try {
      const response = await apiClient.get(`/exchange-rate/convert?amount=${amount}&fromCurrency=${fromCurrency}&toCurrency=${toCurrency}`);
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Exchange rate check failed');
    }
  }

  // Get Transactions
  async getTransactions(page = 1, limit = 20) {
    try {
      const response = await apiClient.get(`/transactions?page=${page}&limit=${limit}`);
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Failed to fetch transactions');
    }
  }

  // Get Transaction by ID
  async getTransactionById(id) {
    try {
      const response = await apiClient.get(`/transactions/${id}`);
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Failed to fetch transaction');
    }
  }

  // Create Transaction
  async createTransaction(transactionData) {
    try {
      const response = await apiClient.post('/transactions', transactionData);
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Failed to create transaction');
    }
  }

  // Upload Invoice for Transaction
  async uploadInvoice(transactionId, file) {
    const formData = new FormData();
    formData.append('file', file);
    formData.append('transactionId', transactionId);

    try {
      const response = await apiClient.post(`/transactions/${transactionId}/upload-invoice`, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Failed to upload invoice');
    }
  }

  // Update Transaction Status
  async updateTransactionStatus(id, status) {
    try {
      const response = await apiClient.patch(`/transactions/${id}/status`, { status });
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Failed to update transaction status');
    }
  }

  // Delete Transaction
  async deleteTransaction(id) {
    try {
      const response = await apiClient.delete(`/transactions/${id}`);
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Failed to delete transaction');
    }
  }
}

export default new TransactionService();
