import { apiClient } from '../utils/api';

class AuthService {
  // User Signup with Magic Link
  async signup(email) {
    try {
      const response = await apiClient.post('/auth/signup', { email });
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Signup failed');
    }
  }

  // Resend Magic Link
  async resendMagicLink(email) {
    try {
      const response = await apiClient.post('/auth/resend-code', { email });
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Failed to resend magic link');
    }
  }

  // Verify Magic Link
  async verifyMagicLink(token) {
    try {
      const response = await apiClient.post('/auth/verify-magic-link', { token });
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Invalid or expired link');
    }
  }

  // Login (Existing Users)
  async login(email, password) {
    try {
      const response = await apiClient.post('/auth/login', { email, password });
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Login failed');
    }
  }

  // Logout
  async logout() {
    try {
      await apiClient.post('/auth/logout');
    } catch (error) {
      console.error('Logout error:', error);
    }
    
    // Clear local storage
    localStorage.removeItem('accessToken');
    localStorage.removeItem('refreshToken');
    localStorage.removeItem('user');
  }

  // Refresh Token
  async refreshToken() {
    const refreshToken = localStorage.getItem('refreshToken');
    if (!refreshToken) {
      throw new Error('No refresh token available');
    }

    try {
      const response = await apiClient.post('/auth/refresh', { refreshToken });
      const data = response.data;
      localStorage.setItem('accessToken', data.accessToken);
      localStorage.setItem('refreshToken', data.refreshToken);
      return data;
    } catch (error) {
      throw new Error('Token refresh failed');
    }
  }

  // Get Current User
  async getCurrentUser() {
    try {
      const response = await apiClient.get('/auth/me');
      return response.data;
    } catch (error) {
      if (error.response?.status === 401) {
        // Token expired, try to refresh
        try {
          await this.refreshToken();
          return this.getCurrentUser();
        } catch (refreshError) {
          // Refresh failed, clear tokens
          this.logout();
          return null;
        }
      }
      return null;
    }
  }

  // Check if user is authenticated
  isAuthenticated() {
    return !!localStorage.getItem('accessToken');
  }

  // Get stored user
  getStoredUser() {
    try {
      const userStr = localStorage.getItem('user');
      if (!userStr) return null;
      return JSON.parse(userStr);
    } catch (error) {
      console.error('Error parsing stored user:', error);
      // Clear corrupted data
      localStorage.removeItem('user');
      return null;
    }
  }
}

export default new AuthService();
