import React, { createContext, useContext, useState, useEffect } from 'react';
import authService from '../api/authService';

const AuthContext = createContext();

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [message, setMessage] = useState('');
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  // Check authentication status on mount
  useEffect(() => {
    const checkAuth = async () => {
      try {
        const storedUser = authService.getStoredUser();
        const hasToken = authService.isAuthenticated();
        
        if (storedUser && hasToken) {
          setUser(storedUser);
          setIsAuthenticated(true);
          setLoading(false);
          return;
        }
        
        // Only try to validate with API if we have a token but no stored user
        if (hasToken && !storedUser) {
          const currentUser = await authService.getCurrentUser();
          if (currentUser) {
            setUser(currentUser);
            setIsAuthenticated(true);
            localStorage.setItem('user', JSON.stringify(currentUser));
          }
        }
      } catch (error) {
        console.error('Auth check failed:', error);
        // Don't log out on API failure - just clear invalid data
        localStorage.removeItem('accessToken');
        localStorage.removeItem('refreshToken');
        localStorage.removeItem('user');
        setUser(null);
        setIsAuthenticated(false);
      } finally {
        setLoading(false);
      }
    };

    checkAuth();
  }, []);

  // Login with email and password
  const login = async (email, password) => {
    setLoading(true);
    setMessage('');
    try {
      const response = await authService.login(email, password);
      setUser(response.user);
      setIsAuthenticated(true);
      localStorage.setItem('accessToken', response.accessToken);
      localStorage.setItem('refreshToken', response.refreshToken);
      localStorage.setItem('user', JSON.stringify(response.user));
      setMessage('Login successful!');
      return response;
    } catch (error) {
      setMessage(error.message || 'Login failed');
      throw error;
    } finally {
      setLoading(false);
    }
  };

  // Signup with magic link
  const signup = async (email) => {
    setLoading(true);
    setMessage('');
    try {
      const response = await authService.signup(email);
      setMessage('Check your email for magic link!');
      return response;
    } catch (error) {
      setMessage(error.message || 'Signup failed');
      throw error;
    } finally {
      setLoading(false);
    }
  };

  // Resend magic link
  const resendMagicLink = async (email) => {
    setLoading(true);
    setMessage('');
    try {
      const response = await authService.resendMagicLink(email);
      setMessage('Magic link resent to your email!');
      return response;
    } catch (error) {
      setMessage(error.message || 'Failed to resend magic link');
      throw error;
    } finally {
      setLoading(false);
    }
  };

  // Verify magic link
  const verifyMagicLink = async (token) => {
    setLoading(true);
    setMessage('');
    try {
      const response = await authService.verifyMagicLink(token);
      setUser(response.user);
      setIsAuthenticated(true);
      localStorage.setItem('accessToken', response.accessToken);
      localStorage.setItem('refreshToken', response.refreshToken);
      localStorage.setItem('user', JSON.stringify(response.user));
      setMessage('Email verified successfully!');
      return response;
    } catch (error) {
      setMessage('Invalid or expired link');
      throw error;
    } finally {
      setLoading(false);
    }
  };

  // Logout
  const logout = async () => {
    setLoading(true);
    try {
      await authService.logout();
      setUser(null);
      setIsAuthenticated(false);
      setMessage('Logged out successfully');
    } catch (error) {
      console.error('Logout error:', error);
      setMessage('Logout completed');
    } finally {
      setLoading(false);
    }
  };

  // Clear message
  const clearMessage = () => {
    setMessage('');
  };

  const value = {
    user,
    loading,
    message,
    isAuthenticated,
    login,
    signup,
    logout,
    resendMagicLink,
    verifyMagicLink,
    clearMessage,
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
};
