import { apiClient } from '../utils/api';

class AdminService {
  // Get Quality Metrics
  async getQualityMetrics(period = '30d') {
    try {
      const response = await apiClient.get(`/admin/transactions/quality-metrics?period=${period}`);
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Failed to fetch quality metrics');
    }
  }

  // Get All Transactions (Admin)
  async getAllTransactions(page = 1, limit = 50, status = null) {
    try {
      let url = `/admin/transactions?page=${page}&limit=${limit}`;
      if (status) {
        url += `&status=${status}`;
      }
      const response = await apiClient.get(url);
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Failed to fetch transactions');
    }
  }

  // Get Transaction Details (Admin)
  async getTransactionDetails(id) {
    try {
      const response = await apiClient.get(`/admin/transactions/${id}`);
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Failed to fetch transaction details');
    }
  }

  // Update Transaction Status (Admin)
  async updateTransactionStatus(id, status, notes = '') {
    try {
      const response = await apiClient.patch(`/admin/transactions/${id}/status`, { 
        status, 
        notes 
      });
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Failed to update transaction status');
    }
  }

  // Get Users List
  async getUsers(page = 1, limit = 50, search = '') {
    try {
      let url = `/admin/users?page=${page}&limit=${limit}`;
      if (search) {
        url += `&search=${search}`;
      }
      const response = await apiClient.get(url);
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Failed to fetch users');
    }
  }

  // Get User Details
  async getUserDetails(id) {
    try {
      const response = await apiClient.get(`/admin/users/${id}`);
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Failed to fetch user details');
    }
  }

  // Suspend User
  async suspendUser(id, reason) {
    try {
      const response = await apiClient.post(`/admin/users/${id}/suspend`, { reason });
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Failed to suspend user');
    }
  }

  // Unsuspend User
  async unsuspendUser(id) {
    try {
      const response = await apiClient.post(`/admin/users/${id}/unsuspend`);
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Failed to unsuspend user');
    }
  }

  // Get System Statistics
  async getSystemStats() {
    try {
      const response = await apiClient.get('/admin/stats');
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Failed to fetch system statistics');
    }
  }

  // Get Audit Logs
  async getAuditLogs(page = 1, limit = 50, action = null, userId = null) {
    try {
      let url = `/admin/audit-logs?page=${page}&limit=${limit}`;
      if (action) url += `&action=${action}`;
      if (userId) url += `&userId=${userId}`;
      
      const response = await apiClient.get(url);
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Failed to fetch audit logs');
    }
  }
}

export default new AdminService();
